<template>
  <div class="userinfo">
      <nav-bar>
      </nav-bar>
       <img src="../assets/img/1.gif" alt="" class="backimg">
       <user-detail :userInfo="model"/>
       <show-ye></show-ye>
  </div>
</template>

<script>
import NavBar from '../components/common/NavBar'
import UserDetail from '../components/usercomponents/UserDetail'
import ShowYe from '../views/shouye'
export default {
  data(){
    return{
model:{}
    }
  },
components:{
  NavBar,
  UserDetail,
  ShowYe
},
methods:{
 async userinfodata(){
const res=await this.$http.get('/user/'+localStorage.getItem('id'),{
  headers:{
    'Authorization':'Bearer '+localStorage.getItem('token')
  }
})
this.model=res.data[0]
console.log(res)
}
},
created(){
this.userinfodata()
},
mounted(){
 document.querySelector('body').setAttribute('style', 'background-color:#f7f7f7')
},
}
</script>

<style lang="less">
.userinfo{
  .backimg{
    width: 100%;
    height: 91px;
    padding: 5px 0;
  }
}
</style>